.job-container {
    color: #ffffff;
    background-color: #272727;
    border-radius: 10px;
    padding: 25px;
    margin-bottom: 20px;
    list-style-type: none;
  }
  .jobItem {
    text-decoration: none;
  }
  .title-name {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .j_title {
    font-size: 22px;
  }
  .rating-container {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .location-emptype-package {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .location-emptype {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .location-logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 10px;
  }
  .company_logo {
    height: 84px;
    width: 84px;
  }
  .star {
    color: #fbbf24;
    margin-right: 10px;
  }
  