.home-container {
    background-image: url('https://assets.ccbp.in/frontend/react-js/home-sm-bg.png');
    height: 100vh;
    background-size: cover;
    text-align: left;
    color: #ffffff;
    padding: 30px;
  }
  .title {
    font-size: 42px;
  }
  .description {
    font-size: 28px;
    width: 50%;
  }
  .job-btn {
    color: #ffffff;
    background-color: #6366f1;
    padding: 10px;
    border-radius: 8px;
    border-style: none;
    margin-top: 30px;
  }
  @media (width>=768px) {
    .home-container {
      background-image: url('https://assets.ccbp.in/frontend/react-js/home-lg-bg.png');
    }
    .title {
      font-size: 70px;
      width: 60%;
    }
    .description {
      font-size: 32px;
    }
  }
  