.jobItem-container {
    background-color: #000000;
    display: flex;
    flex-direction: column;
    padding: 5px;
  }
  .loader-container {
    height: 100vh;
  }
  .job-container {
    color: #ffffff;
    background-color: #272727;
    border-radius: 10px;
    padding: 25px;
    margin: 5px;
  }
  .title-name {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .title {
    font-size: 26px;
  }
  .rating-container {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .location-emptype-package {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .location-emptype {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .location-logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 10px;
  }
  .company_logo {
    height: 64px;
    width: 64px;
  }
  .star {
    color: #fbbf24;
    margin-right: 10px;
  }
  .description-vedio {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .skills-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .skill {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 10px;
    list-style-type: none;
  }
  .life_at_company {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .visit-btn {
    background-color: transparent;
    border-style: none;
    color: #b6c5ff;
    font-size: 20px;
  }
  .img_at_company {
    height: 350px;
    width: 350px;
    margin-left: 20px;
    order: 2;
  }
  .desc {
    font-size: 22px;
  }
  .failure-container {
    height: 100vh;
  }
  .head {
    color: #ffffff;
  }
  .similar-jobs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  @media (width>=768px) {
    .jobItem-container {
      padding: 30px;
    }
    .life_at_company {
      flex-direction: row;
    }
    .company_logo {
      height: 84px;
      width: 84px;
    }
    .skill {
      margin: 50px;
    }
  }
  