.similar-job-container {
    color: #ffffff;
    background-color: #272727;
    border-radius: 10px;
    padding: 15px;
    margin: 10px;
    width: 300px;
  }
  .title-name {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .s_title {
    font-size: 20px;
  }
  .rating-container {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .location-emptype-package {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .location-emptype {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .location-logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 10px;
  }
  .company_logo {
    height: 84px;
    width: 84px;
    margin-right: 5px;
  }
  .star {
    color: #fbbf24;
    margin-right: 10px;
  }
  @media (width>=768px) {
    .title {
      font-size: 28px;
    }
    .company_logo {
      margin-right: 10px;
    }
  }
  