.jobs-container {
    background-color: #000000;
    display: flex;
    flex-direction: column;
    padding: 30px;
  }
  @media (width>=768px) {
    .jobs-container {
      flex-direction: row;
    }
  }
  .profile-filter-container {
    width: 310px;
  }
  .profile-container {
    background-image: url('https://assets.ccbp.in/frontend/react-js/profile-bg.png');
    background-size: cover;
    width: 250px;
    padding: 20px;
    border-radius: 20px;
    margin:10px;
  }
  .profile-name {
    color: #6366f1;
    font-size: 22px;
  }
  .profile-bio {
    color: #475569;
  }
  .emptypeList {
    list-style-type: none;
  }
  .salaryList {
    list-style-type: none;
  }
  .label-type {
    color: #ffffff;
  }
  .loader-container {
    display: flex;
  }
  .jobs-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    overflow-y: auto;
  }
  .jobList {
    list-style-type: none;
  }
  .retry-btn {
    color: #ffffff;
    background-color: #4f46e5;
    padding: 10px;
    border-style: none;
    border-radius: 8px;
  }
  .jobslist-container {
    padding: 20px;
  }
  .nojobs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #ffffff;
  }
  .search-input {
    background-color: transparent;
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 14px;
    font-weight: 500;
    border:none;
    outline: none;
    flex-grow: 1;
    padding-left: 16px;
    padding-top:6px;
    padding-bottom: 6px;
    padding-right: 16px;
    width: 200px;
  }
  .search-container {
    display: none;
  }
  .icon-container {
    background-color: #475569;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 10px 20px;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    margin:0px;
  }
  .search-icon{
    color:#ffffff;
    width:15px;
    height: 15px;
  }
  @media (width>=768px) {
    .profile-container {
      background-image: url('https://assets.ccbp.in/frontend/react-js/profile-bg.png');
      background-size: cover;
      width: 300px;
      padding: 20px;
      border-radius: 20px;
    }
    .search-container-sm {
      display: none;
    }
    .search-container {
      display: flex;
      border: 1px solid #7e858e;
      border-radius: 5px;
    }
    .search-input {
      background-color: transparent;
      color: #ffffff;
      font-family: 'Roboto';
      font-size: 14px;
      font-weight: 500;
      border:none;
      outline: none;
      flex-grow: 1;
      padding-left: 16px;
      padding-top:6px;
      padding-bottom: 6px;
      padding-right: 16px;
      width: 450px;
    }
    .jobs-list-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 20px;
    }
  }
  