.header-container {
    background-color: #202020;
    padding: 5px;
  }
  .header-sm {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .header-options {
    display: flex;
    flex-direction: row;
    color: #ffffff;
    list-style-type: none;
  }
  .logoutBtn {
    list-style-type: none;
  }
  .logout-btn {
    color: #ffffff;
    background-color: #4f46e5;
    border-radius: 10px;
    border-style: none;
    padding: 10px;
  }
  .option {
    color: #ffffff;
    margin-left: 10px;
    text-decoration: none;
  }
  .icons {
    color: #ffffff;
    height: 50px;
    margin-right: 20px;
  }
  .header-lg {
    display: none;
  }
  .header-icons {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  @media (width>=768px) {
    .header-lg {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      height: 60px;
    }
    .website-logo {
      height: 64px;
      width: 100px;
    }
    .header-sm {
      display: none;
    }
  }
  